:root {
    /* Colors */
    --red: #f8333c;
    --background: #f7f7f2;
    --foreground: #46494c;

    --border-radius: 70px;
    --box-shadow: 8px 8px 13px #cbcbc6, -8px -8px 13px #ffffff;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

body {
    background-color: var(--background);
    color: var(--foreground);
    display: grid;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    font-size: 1rem;
    font-weight: 400;
    height: 100vh;
    line-height: 1.5;
    margin: 0;
    place-items: center;
}

p {
    margin-bottom: 1rem;
    margin-top: 0;
}

button {
    border-radius: 0;
    cursor: pointer;
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
    margin: 0;
    overflow: visible;
    text-transform: none;
}

button:focus {
    outline: none;
}

.timer__current {
    font-size: 1.25rem;
    text-align: center;
    text-transform: uppercase;
}

.timer__time {
    font-size: 3.052rem;
    text-align: center;
}

.timer__card {
    border-radius: var(--border-radius);
    box-shadow: var(--box-shadow);
    margin-bottom: 1.75rem;
    width: 16rem;
    padding: 2.75rem 1.15rem;
}

.timer__pad {
    display: grid;
    grid-template-areas: "start reset" "plus minus" "change change";
    gap: 1rem;
}

.pad__start {
    grid-area: start;
}

.pad__plus-five {
    grid-area: plus;
}

.pad__minus-five {
    grid-area: minus;
}

.pad__reset {
    grid-area: reset;
}

.pad__change {
    grid-area: change;
}

.btn {
    border: none;
    padding: 1.75rem;
    border-radius: 25px;
    box-shadow: var(--box-shadow);
}

.btn-red {
    color: var(--red);
}
